.markmap-node {
  cursor: pointer;
}

.markmap-node-circle {
  fill: #fff;
  stroke-width: 1.5px;
}

.markmap-node-text {
  fill:  #000;
  font: 10px sans-serif;
}

.markmap-link {
  fill: none;
}

svg#mindmap {
  width: 100%;
  height: 730px;
}



